import React, { useState } from "react";
import { db } from "../firebase";
import PageHelmet from "../component/common/Helmet";
import Header from "../component/header/HeaderDetails";


const Contact = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");

  const [loader, setLoader] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();
    setLoader(true);

    db.collection("contacts")
      .add({
        name: name,
        email: email,
        message: message,
      })
      .then(() => {
        setLoader(false);
        alert("Your message has been submitted");
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });

    setName("");
    setEmail("");
    setMessage("");
  };

  return(
    <React.Fragment>
      <PageHelmet pageTitle='Contact Me' />
      <Header logo="symbol-dark"/>

    <form className="form defaultMargin" onSubmit={handleSubmit}>
      <h1 className="form-title">Hey! Tell me all about your project  </h1>

      <div className="bottom-line">
        <input
          placeholder="Name"
          value={name}
          onChange={(e) => setName(e.target.value)}
        />
      </div>

      <div className="bottom-line">
        <input
          placeholder="Email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        />
      </div>

      <div className="bottom-line">
        <textarea
          placeholder="Message"
          value={message}
          onChange={(e) => setMessage(e.target.value)}
        ></textarea>
      </div>

      <button
        type="submit"
        className="rn-button-style--2 btn-solid"
      >
        Submit
      </button>
    </form>
    </React.Fragment>


  );
};

export default Contact;
